.middle{
    width: 100%;
    min-height: 80vh;
    padding-top: 13rem;
}
.middlemain{
    width: 1150px;
    margin: auto;

}
.showbar{
    margin: 1em 0;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Libre Baskerville';
    color: #ffffff;
}
.spanhead{
    font-size: 2rem;
    position: relative;
}
.spanhead::before{
    background: #0259EB;
    content: '';
    position: absolute;
    width: 50px;
    height: 3px;
    border-radius: 5px;
    bottom: 10%;
}
.spanbtn{
    color: #E5C558;
    font-family: 'Nunito';

}

.celebcards,.reviewcards{
    margin: 2em 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.celebcards{
    margin-bottom: 6rem;
}