header {
    width: 100%;
    height: 10vh;
}
header nav {
    padding: 0 0rem;
    margin: auto;
    width: 1100px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
header nav ul{
    color: #FAFAFA;
    font-family: 'Open Sans';
    font-size: .8em;
    transition: .5s;
}
header nav ul li{
    transition: .5s;
}
header nav ul li:hover{
    color: rgb(70, 70, 233);
    cursor: pointer;
}
.logo{
    background: url(../../images/logo.png);
    width: 86px;
    height: 44px;
}
.list{
    display: flex;
    width: 280px;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}
.search{
    display: flex;
    align-items: center;
}
.searchIcon{
    margin: 0 .5em;
    display: block;
    width: 13px;
    height: 15px;
    background-image: url(../../images/search.png);
    background-repeat: no-repeat;
}
.cart{
    background-image: url(../../images/Basket.png);
    background-position: center;
    width: 13px;
    height: 15px;
}